import React, { useEffect, useState, useRef } from 'react';

import {  getTextShadow } from '../../utils/common';
import Countdown from '../Countdown/Countdown';

import './Billet.css';

const Billet = () => {
  const [textShadow, setTextShadow] = useState(getTextShadow());

  const intervalRef = useRef();

  const style = {
    textShadow,
  }

  useEffect(
    () => {
      intervalRef.current = setInterval(() => {
        setTextShadow(getTextShadow());
      }, 13000)

      return () => clearInterval(intervalRef.current);
    },
    []
  );

  return (
    <div className="billet" style={style}>
      <span className="billet__title">   </span>
      <Countdown date={`2021-12-29T00:00:00`} />
    </div>
  );
}

export default Billet


